<template>
  <div class="news">
    <!-- <van-nav-bar title="直推第一代统计" left-arrow @click-left="routerBack"/> -->
    <van-list class="list" v-model="loading" :finished="finished" finished-text="" @load="onLoad">
      <div class="list-title">
        <div>{{$t('index.accountID')}}</div>
        <div class="center">{{$t('index.VIPlevel')}}</div>
        <div class="center">{{$t('index.shareAddress')}}</div>
        <div>{{$t('index.teamPerformance')}}</div>
      </div>
      <div class="list-item" v-for="item in list" :key="item.id">
        <div>{{item.invite_code}}</div>
        <div class="center">{{item.level}}</div>
        <div class="center">{{item.child}}</div>
        <div class="">{{item.team_performance}}</div>
      </div>
    </van-list>
  </div>
</template>

<script>
export default {
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      page: 1,
    }
  },
  methods: {
    onLoad() {
      const params = {
        page: this.page,
      }
      this.page++;
      this.$http('/user/child', {params: params}).then(response => {
        let res = response.data.data;
        this.info = res;
        console.log(res);
        res.data.forEach(element => {
          this.list.push(element);
        });
        this.loading = false;
        if (res.data.length < res.per_page) {
          this.finished = true;
        }
      });
    },
  }
}
</script>

