<script>
export default {
  data() {
    return {
      info: {1: 0,2:0,3:0,4:0},
      reward: {},
      reward2: {},
      stage: '',
    };
  },
  created() {
    this.getInfo();
    this.getReward();
  },
  methods: {
    // 奖池数据
    getInfo() {
      this.$http("/jackpot/pool").then(response => {
        const res = response.data;
        this.info = res.data;
      })
    },
    // 中奖记录
    getReward() {
      this.$http("/jackpot/award").then(response => {
        const res = response.data.data;
        console.log(res.data);
        if(res.data.length > 0) {
         this.reward = res.data[0];
        }else{
          this.getReward2();
        }
      })
    },
    // 奖池数据
    getReward2() {
      this.$http("/jackpot/inspire").then(response => {
        const res = response.data.data;
        console.log(res);
        if(res.data.length > 0) {
         this.stage = response.data.msg;
         this.reward2 = res.data[0];
        }
      })
    },
  }
};
</script>

