等于上一期投资金额，不可小于；"),_c('br'),_vm._v("\n3.  可手动输入符合该期投资区间的续投金额；"),_c('br'),_vm._v("\n4.  用户可以在智筹明细列表内切换手动续投和自动续投的类型；"),_c('br'),_vm._v("\n5.  延期续投：如果选择不进行续投或忘记续投，该收益总额将留在合约资金池内，直到玩家使用续投功能。延期续投在对应的期数结算时，标准投资仍可获得5%的利润与本金，次级投资可获得2%的利润与本金（即延期续投的利润比有效续投的利润减少10%）。玩家本次延期续投的投资额，则可在规定期数时正常产生15%（或12%）的利润及本金返还；"),_c('br'),_vm._v("\n6.  如果玩家没有意愿选择继续续投，可以点击回收功能，一旦回收将不会产生任何投资利润。在回收时所在的期数结束后，间隔两期后将投资的本金（无利润），扣除5%的管理费，返回至用户账户余额内；"),_c('br'),_vm._v("\n7.  玩家可以在投资本期之后，本期与接下来的三期智筹时间内，即在第四期结束之前进行手动续投都视为有效续投。"),_c('br'),_c('br'),_vm._v("\n自动续投"),_c('br'),_vm._v("\n1.  启用该功能后，系统将隔两期之后自动为玩家进行续投，账户余额将自动扣除与上一次投资相同的金额，玩家须在余额内保证留有足够的金额，以确保自动续投成功；"),_c('br'),_vm._v("\n2.  如果余额不足，上一次的收益总额将留在合约资金池内，直到玩家在智筹明细列表中使用手动续投功能（属于延期续投）。延期续投在对应的期数结算时，标准投资仍可获得5%的利润与本金，次级投资可获得2%的利润与本金（即延期续投的利润比有效续投的利润减少10%）。玩家本次延期续投的投资额，则可在规定期数时正常产生15%（或12%）的利润及本金返还；"),_c('br'),_vm._v("\n3.  如果配合使用自动续投功能，建议第一次投资者在钱包余额内留够至少2倍投资额，此后系统将自动循环为投资者自动投资和收益，是一种非常方便的智能理财方案。"),_c('br'),_c('br'),_vm._v("\n名词解释："),_c('br'),_vm._v("\n智筹目标1% ：是指点击后自动输入用户可以参与的本期最大投资额，数值为每期智筹目标（每期35%递增）总金额的1%；"),_c('br'),_c('br'),_vm._v("\n标准投资最低额：是指点击后自动输入用户可以参与的本期标准投资额范围的最小值；"),_c('br'),_c('br'),_vm._v("\n标准投资范围："),_c('br'),_vm._v("\n1期 -10期，  投资额10U~1%（每期总智筹目标金额的1%）；"),_c('br'),_vm._v("\n11期 -20期，投资额100U~1%；"),_c('br'),_vm._v("\n21期 -30期，投资额1000U~1%；"),_c('br'),_vm._v("\n31期 -35期，投资额3000U~1%；"),_c('br'),_vm._v("\n36期 -40期，投资额5000U~1%；"),_c('br'),_vm._v("\n41期以后，  投资额10000U~1%；"),_c('br'),_c('br'),_vm._v("\n同上次投资额：是指点击后自动输入用户上一次相等的投资额；"),_c('br')])]):_vm._e()])}
