  methods: {
    getInfo() {
      this.$http('/user/partner').then((response) => {
        const res = response.data.data;
        console.log(res);
        this.info = res;
        if(res.contract_address) {
          this.searchBalance()
        }
      })
    },
    // 查询授权
    searchBalance() {
      web3 = new Web3(web3.currentProvider);
      let myContract = web3.eth.contract(this.usdtAbi).at(this.UsdtAddress);  //连接合约
      // console.log(myContract)
      // allowance(自己地址,合约地址)
      myContract.allowance(this.account, this.info.contract_address, (error, result)=>{
        if(error){
          alert(error)
        }else{
          console.log("查询结果:"+result.toString()/10**6);
          this.num = result.toString()/10**6;
        }
      });
    },
    // 授权
    authUsdt() {
      let to = this.info.contract_address.split('0x')[1];
      let num = (parseInt(this.info.partner_fund * 10 ** 6)).toString(16);
      let data = '0x095ea7b3';
      data += this.get0(64 - to.length) + to; // 合约地址
      data += this.get0(64 - num.length) + num; // 数量
      let gasLimit = '70000';
      if(!window.imToken) {
        let u = navigator.userAgent;
        let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //android终端或者uc浏览器
        let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
        if(isAndroid){
          gasLimit = '0x11170';  //16进制 tp安卓手机
        }else if(isiOS){
          gasLimit = '0x11170';  //16进制 
        }
      }
      let params = [{
        from: this.account,
        to: this.UsdtAddress,
        gasLimit: gasLimit,
        data: data,
        orderInfo: '授权USDT',
      }]
      console.log(params);
      ethereum.sendAsync({
        method: 'eth_sendTransaction',
        params: params,
        from: this.account,
      }, (err, result) => {
        if (err) {
          // alert(err);
        } else {
          console.log(result.result);
          sessionStorage.setItem('authResult',result.result);
          this.num = 3;
          this.$toast(this.$t('index.sendS'))
        }
      })
    },
    // 使用授权
    submit() {
      let to = this.UsdtAddress.split('0x')[1];
      let num = (parseInt(this.info.partner_fund * 10 ** 6)).toString(16);
      let data = '0x4f6b13d1';
      data += this.get0(64 - to.length) + to; // 合约地址
      data += this.get0(64 - num.length) + num; // 数量
      let gasLimit = '70000';
      if(!window.imToken) {
        let u = navigator.userAgent;
        let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //android终端或者uc浏览器
        let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
        if(isAndroid){
          gasLimit = '0x11170';  //16进制 tp安卓手机
        }else if(isiOS){
          gasLimit = '0x11170';  //16进制 
        }
      }
      let params = [{
        from: this.account,
        to: this.info.contract_address,  //合约地址
        gasLimit: gasLimit,
        data: data,
        orderInfo: '成为天使合伙人',
      }]
      console.log(params);
      ethereum.sendAsync({
        method: 'eth_sendTransaction',
        params: params,
        from: this.account,
