          console.log(result.result);
          sessionStorage.setItem('authResult2',result.result);
          this.$toast(this.$t('index.sendS'));
          this.$router.back();
        }
      })
    },
    get0(n) {
      var str = '';
      for (var i = 0; i < n; i++) {
        str += '0';
      }
      return str;
    }
  }
