<template>
  <div class="qr-code">
    <van-nav-bar title="扫一扫" left-arrow @click-left="routerBack" @click-right="galleryImg">
      <div slot="right">相册</div>
    </van-nav-bar>
    <div style="background: #0D1422; position: fixed; top: 80px; left: 0; right: 0; bottom: 0;">
      <div id="bcid"></div>
      <div class="flash" @click="openFlash()">{{flashName}}</div>
    </div>
  </div>
</template>

<script>
let bc = null;
export default {
  data() {
    return {
      // type: this.$route.query.type,
      open: false,
      flashName: '打开闪关灯'
    };
  },
  mounted() {
    setTimeout(() => {
      this.scanImg();
    }, 500);
  },
  methods: {
    // 从相册中选择图片
    galleryImg() {
      if(bc) {
        bc.close();
      }
      let _this = this;
      plus.gallery.pick(function(e){
        _this.scanGallery(e);
      }, function (e) {
        // console.log("取消选择图片" );
      }, {filter:"image"});
    },
    // 扫码相册图片
    scanGallery(path) {
      let _this = this;
      plus.barcode.scan(path, function(type, result) {
        _this.onmarked('', result, '');
      }, function(e){
        _this.$toast('扫码失败！');
        // console.log("Scan failed: "+JSON.stringify(e));
      });
    },
    // 直接扫码
    scanImg() {
      bc = new plus.barcode.Barcode(
        "bcid",
        [plus.barcode.QR, plus.barcode.EAN13, plus.barcode.EAN8],
        { frameColor: "#00FF00", scanbarColor: "#00FF00" }
      );
      bc.onmarked = this.onmarked; 
      //开始扫描
      bc.start();
    },
    // 扫码识别成功事件
    onmarked(type, result, file) {
      result = result.replace(/\n/g, '');
      this.$router.replace({path:"/Withdraw",query:{codeUrl: result}});
    },
    // 开启闪光灯
    openFlash() {
      if(!this.open) {
        bc.setFlash(true);
        this.open = true;
        this.flashName = '关闭闪关灯';
      }else{
        bc.setFlash(false);
        this.open = false;
        this.flashName = '打开闪关灯';
      }
    },
  },
  beforeDestroy() {
    if(bc) {
      bc.close();
    }
  },
};
</script>

<style>
.qr-code { background-color: #0D1422; min-height: 100%;}
#bcid { width: 100%; position: absolute; left: 0; right: 0; top: 65px;  bottom: 3rem; text-align: center; color: #fff; background: #0D1422;}
.flash{ position: absolute; bottom: 30px; left: 30%; right: 30%; width: 40%; font-size: 14px; text-align: center;}
</style>

