import Vue from 'vue'
import App from './App.vue'
import router from './router'
import axios from 'axios'
import store from './store.js'
import Vant from 'vant';
import 'vant/lib/index.css';
import './assets/css/main.css'; //全局样式
import i18n from '@/locales'
import VueClipboard from 'vue-clipboard2' //复制粘贴复制插件

Vue.config.productionTip = false;
Vue.use(Vant);
Vue.use(VueClipboard)
Vue.prototype.routerBack = function() {
    router.back();
}

axios.defaults.baseURL = 'https://api.ambcoin.io';
// axios.defaults.baseURL = 'https://amb.8vp4ra.cn';
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
axios.defaults.headers.token = localStorage.getItem('accessToken') || '';
axios.defaults.headers['Accept-Language'] = localStorage.getItem('localeLanguage') || 'zh-cn';
Vue.prototype.$http = axios;

document.title = 'AMB智筹';
//路由全局守卫
router.beforeEach((to, from, next) => {
    const isLogin = localStorage.getItem("accessToken") ? true : false;
    if(to.meta.title) {
        document.title = to.meta.title;
    }else{
        document.title = 'AMB智筹';
    }
    if (isLogin || to.path == '/Login' || to.path == '/Register' || to.path == '/Forget') {
        next();
    } else {
        next("/Login");
    }
})
import { Toast } from 'vant';
Vue.use(Toast);
// 响应拦截器 
axios.interceptors.response.use(
    response => {
        const res = response.data;
        // console.log(res);
        switch (res.code) {
            case 997: // 账号不存在
                router.push("/Login");
                axios.defaults.headers.token = '';
                Toast(res.msg);
                break;
            case 998: // 账号不存在
                localStorage.clear();
                sessionStorage.clear();
                router.push("/Login");
                axios.defaults.headers.token = '';
                Toast(res.msg);
                break;
            case 999: // 登陆过期
                localStorage.clear();
                sessionStorage.clear();
                router.push("/Login");
                axios.defaults.headers.token = '';
                Toast(res.msg);
                break;
            case 995: // 账号在其他设备登陆
                router.push("/Login");
                axios.defaults.headers.token = '';
                Toast(res.msg);
                break;
            case 996: // 账号已注销
                router.push("/Login");
                axios.defaults.headers.token = '';
                Toast({message:res.msg, duration:5000});
                break;
        }
        return response;
    },
    error => {
        Toast("Network Error!");
        return Promise.reject(error);
    }
)
// 全局header公共组件
import AppBar from './components/AppBar'
Vue.component('app-bar', AppBar)
let vueInstance = new Vue({
    render: h => h(App),
    router,
    store,
    i18n,
}).$mount('#app')