<template>
	<div class="content">
    <!-- <van-nav-bar title="地址簿" left-arrow @click-left="routerBack"/> -->
		<div class="top">
			<div class="left">USDT</div>
			<div>ERC20</div>
		</div>
		<van-cell-group>
			<div class="lable">{{$t('index.name')}}</div>
      <van-field v-model="validateForm.name" :placeholder="$t('index.inputName')" clearable/>
			<div class="lable">{{$t('index.address')}}</div>
      <van-field v-model="validateForm.address" :placeholder="$t('index.inputAddress')" right-icon="scan" @click-right-icon="toScan" clearable/>
      <div class="delete" @click="deleteAddrss">{{$t('index.deleteAddress')}}</div>
    </van-cell-group>
		<button class="common-btn" @click="submit">{{$t('index.save')}}</button> 
	</div>
</template>

<script>
	export default {
		data() {
			return {
				address: {},
				validateForm: {
					name: '',
					address: '',
				},
				list: [],
			}
		},
		created() {
			if(this.$route.query.address) {
				this.address  = JSON.parse(this.$route.query.address);
				this.validateForm.name = this.address.nick;
				this.validateForm.address = this.address.address;
			}
		},
		methods: {
			// 保存
			submit(address) {
				const formData = {
					id: this.address.id,
					nick: this.validateForm.name,
					address: this.validateForm.address,
				}
				this.$http.post('/user/addressEdit', formData).then((response) => {
					const res = response.data;
					this.$toast(res.msg);
					if(res.code === 0) {
						this.$router.back();
					}
				})
			},
			// 删除地址
			deleteAddrss() {
				const formData = {
					id: this.address.id,
				}
				this.$http.post('/user/addressDel', formData).then((response) => {
					const res = response.data;
					this.$toast(res.msg);
					if(res.code === 0) {
						this.$router.back();
					}
				})
			},
			toScan() {
				this.$router.push('Scan')
			}
		}
	}
</script>

<style scoped>
.top{ width: 92%; margin: 10px 4%; padding: 12px 4%; display: flex; justify-content: space-between; background: #121A33; font-size: 15px; border-radius:5px;}
.top .left{ color: #B9BFD9;}
.delete{ margin-top: 30px; padding: 12px 4%; display: flex; justify-content: center; background: #121A33; font-size: 15px; border-radius:5px; color: #FEB958;}
.common-btn{ position: fixed; bottom: 30px; left: 4%; height: 45px; line-height: 45px; width: 92%; box-shadow: none; }
</style>
