<template>
	<div class="content">
    <!-- <van-nav-bar title="地址簿" left-arrow @click-left="routerBack"/> -->
		<div class="list" v-if="list.length>0">
			<div class="list-item" v-for="(item,index) in list" :key="index">
				<div class="left" @click="selectAddress(item.address)">
					<div class="name">{{item.nick}}  <span>丨 USDT</span></div>
					<div class="address">{{item.address}}</div>
				</div>
				<router-link class="right" :to="{path: '/AddressAdd', query:{address: JSON.stringify(item)}}">
					<div class="text">{{$t('index.edit')}}</div>
				</router-link>
			</div>
		</div>
    <router-link v-else class="add" to="/AddressAdd">
      <van-icon name="plus" />{{$t('index.addAddress')}}
    </router-link>
		<van-button class="common-btn" to="/AddressAdd">{{$t('index.newAddress')}}</van-button> 
	</div>
</template>

<script>
	export default {
		data() {
			return {
				list: [],
			}
		},
		created() {
			this.getAddressList();
		},
		methods: {
			getAddressList() {
				this.$http('/user/address').then((response) => {
					const res = response.data.data;
					if(res.data.length>0) {
						this.list = res.data;
					}
				})
			},
			// 选择地址
			selectAddress(address) {
				localStorage.setItem('address', address);
				this.$router.back();
			}
		}
	}
</script>

<style scoped>
.list{ display: flex; flex-direction: column; padding: 10px 4%;}
.list-item{ padding: 15px 4%; display: flex; justify-content: space-between; align-items: center; border-bottom:1px solid #19223E; line-height: 24px;}
.list-item .left{ width: 90%; word-wrap: break-word;}
.list-item .name{ margin-right: 10px; font-size: 16px; color: #B9BFD9;}
.list-item .name span{ font-size: 12px; }
.list-item .name .address{ }
.list-item .right .text{ word-break: keep-all; color: #1AC3E9;}
.default{ padding-left: 6px; color: #FF5300; font-weight: normal; font-size: 12px;}
.common-btn{ position: fixed; bottom: 30px; left: 4%; height: 45px; line-height: 45px; width: 92%; box-shadow: none; }
.add{ margin: 40% auto; width: 120px; height: 38px;  display: flex; align-items: center; justify-content: center; background:rgba(26,195,233,0.1); border-radius:5px; color: #1AC3E9;}
.add .van-icon{ color: #1AC3E9;}
</style>
