<template>
  <div class="share">
    <!-- <van-nav-bar title="充币" left-arrow @click-left="routerBack"/> -->
    <div class="top">
      <div class="title">
        <div class="left">USDT</div><div>ERC20</div>
      </div>
    </div>
    <van-cell-group>
      <div class="lable">{{$t('index.addressRecharge')}}<span>（*{{$t('index.addressInfo')}}）</span></div>
      <div readonly style=" padding:10px 0; font-size:12px; color:#fff; border-bottom: 1px solid #19223E!important;">{{address}}
      </div>
      <div class="lable">{{$t('index.numberDeposits')}}</div>
      <van-field v-model="number" :placeholder="$t('index.least10')">
        <template #button style="line-height: 12px;">
          <span class="all">USDT</span>
        </template>
      </van-field>
      <div class="tips2">*{{$t('index.addressInfo2')}}</div>
      <van-button class="common-btn" @click.prevent="submit()">{{$t('index.recharge')}}</van-button>
    </van-cell-group>
		
    <!-- <div class="part">
      <div class="QRcode">
        <div id="qr"></div>
      </div>
      <div class="text">{{$t('index.depositDddress')}}</div>
      <div class="address">{{address}}</div>
      <div class="copy" v-clipboard:copy="address" v-clipboard:success="onCopy" v-clipboard:error="onError">{{$t('index.copyAddress')}}</div>
    </div> -->
    <div class="tips">
      *{{$t('index.rechargeInfo')}}
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      account: JSON.parse(localStorage.getItem('userInfo')).username,
      address: JSON.parse(localStorage.getItem('config')).contract_address,
      shareBtn: true,
      btnStatus: false,
      number: '',
    }
  },
  mounted() {
    // 缓存，静默请求
    // this.info = JSON.parse(localStorage.getItem("share"));
    // let width = 144;
    // let qr = new QRCode(document.getElementById("qr"), {
    //   width: width,
    //   height: width,
    //   colorDark: "#000000",
    //   colorLight: "rgba(255,255,255,1)"
    // });
    // qr.makeCode(this.address);
  },
  methods: {
    // 充值转账
    submit() {
      if(parseFloat(this.number) < 0.001 || !this.number) {
        this.$toast(this.$t('index.least10'));
        return;
      }
      let to = this.address.split('0x')[1];  //充值地址
      let num = (parseInt(this.number * 10 ** 6)).toString(16);
      let data = '0xa9059cbb';
      data += this.get0(64 - to.length) + to;
      data += this.get0(64 - num.length) + num;
      let gasLimit = '70000';
      if(!window.imToken) {
        let u = navigator.userAgent;
        let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //android终端或者uc浏览器
        let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
        if(isAndroid){
          gasLimit = '0x11170';  //16进制 tp安卓手机
        }else if(isiOS){
          gasLimit = '0x11170';  //16进制 
        }
      }
      let params = [{
        to: '0xdac17f958d2ee523a2206206994597c13d831ec7', //usdt代币地址
        from: this.account,
        gasLimit: gasLimit,  
        feeCustomizable: true,
        orderInfo:'充值'+this.number+'USDT',
        data: data,
      }]
      ethereum.sendAsync({
          method: 'eth_sendTransaction',
          params: params,
          from: this.account, // Provide the user's account to use.
        }, (err, result) => {
        if (err) {
          // Handle the error
          console.log(this.$t('index.sendFailed'));
          this.$toast(this.$t('index.sendSuccess'));
        } else {
          console.log(result.result);
          this.$toast($t('index.sendSuccess'))
        }
      })
    },
    get0(n) {
      var str = '';
      for (var i = 0; i < n; i++) {
        str += '0';
      }
      return str;
    },
    //复制地址
    onCopy: function (e) {
      this.$toast(this.$t('index.copyS'));
    },
    onError: function (e) {
      this.$toast(this.$t('index.copyF'));
    },
  }
}
</script>

