<template>
  <div class="wallet-log">
    <!-- <van-nav-bar title="充币明细" left-arrow @click-left="routerBack"/> -->
    <!-- USDT -->
    <van-list class="list" v-model="loading" :finished="finished" :finished-text="$t('index.noMore')" @load="onLoad">
      <div class="list-item" v-for="item in list" :key="item.id">
        <div class="item01"><span class="text">USDT</span><span class="num">{{item.status_text}}</span></div>
        <div class="item02">
          <div><span>{{$t('index.time')}}</span><font>{{item.create_time}}</font></div>
          <div><span>{{$t('index.number')}}</span><font>{{item.num}}</font></div>
          <div><span>{{$t('index.fee')}} (USDT)</span><font>{{item.fee}}</font></div>
        </div>
        <div class="item03">
          <div>{{$t('index.address')}}</div>
          <div class="address"><span>{{item.address}}</span><img src="../../assets/img/copy.png" alt="" v-clipboard:copy="item" v-clipboard:success="onCopy" v-clipboard:error="onError"></div>
        </div>
        <div v-if="item.hash" class="item03">
          <div>HASH</div>
          <div class="address"><span>{{item.hash}}</span><img src="../../assets/img/copy.png" alt="" v-clipboard:copy="item.hash" v-clipboard:success="onCopy" v-clipboard:error="onError"></div>
        </div>
      </div>
    </van-list>
  </div>
</template>

<script>
export default {
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      page: 1,
    }
  },
  methods: {
    // usdt充币记录
    onLoad() {
      const params = {
        page: this.page,
      }
      this.page++;
      this.$http('/wallet/recharge', {params: params}).then(response => {
        let res = response.data.data;
        this.info = res;
        console.log(res);
        res.data.forEach(element => {
          this.list.push(element);
        });
        this.loading = false;
        if (res.data.length < res.per_page) {
          this.finished = true;
        }
      });
    },
    //复制地址
    onCopy: function (e) {
      this.$toast(this.$t('index.copyS'));
    },
    onError: function (e) {
      this.$toast(this.$t('index.copyF'));
    },
  }
}
</script>

