<script>
export default {
  data () {
    return {
      userInfo: {usdt: '0.0000'},
      show: false,
      autoDouble: false,
      autoCash: false
    }
  },
  created() {
    this.getUserInfo();
  },
  methods: {
    // 用户信息
    getUserInfo() {
      this.$http("/user/info").then(response => {
        const res = response.data.data;
        console.log(res);
        this.userInfo = res;
        localStorage.setItem("userInfo", JSON.stringify(res));
      });
    },
    // 弹出
    showPopup() {
      this.show = true;
    },
    // 自动复投
    submit() {
      const formData = {
        auto: '1',
      }
      this.$http.post('/user/auto', formData).then((response) => {
        const res = response.data;
        this.$toast(res.msg);
        // console.log(res)
        if(res.code === 0) {
          this.show = false;
          this.getUserInfo();
        }
      })
    },
  }
}
</script>

