<template>
  <div class="ore">
    <!-- <van-nav-bar title="获奖公告" left-arrow @click-left="routerBack"/> -->
    <div v-for="(item,index) in rewardList" :key="index">
      <div class="reward">
        <div class="reward-title" v-if="localeLanguage == 'en-us'">{{item.stage}}th award announcement</div>
        <div class="reward-title" v-else>第{{item.stage}}期获奖公告</div>
        <div class="reward-item">
          <img src="../../assets/img/cup.png" alt="">
          <div class="right">
            <div class="text" v-if="localeLanguage == 'en-us'"><span>UID {{item.uid}}</span>Won a special start-up award, direct promotion <font>VIP{{item.level}}</font></div>
            <div class="text" v-else><span>UID {{item.uid}}</span>获得特别启动奖，直升 <font>VIP{{item.level}}</font></div>
            <div>{{item.address}}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="reward-title" v-if="localeLanguage == 'en-us'">The {{stage}} incentive pool award announcement (issued after the {{stage+3}} period ends)</div>
    <div class="reward-title" v-else>第{{stage}}期激励池获奖公告（第{{stage+3}}期结束后发放）</div>
    <div v-for="(item,index) in rewardList2" :key="index">
      <div class="reward">
        <div class="reward-item">
          <img src="../../assets/img/cup.png" alt="">
          <div class="right">
            <div class="text" v-if="localeLanguage == 'en-us'"><span>UID {{item.uid}}</span> {{index+1}} in the incentive pool reward, direct address number of<font>{{item.child}}</font>，Get bonus<font>{{item.num}} U</font></div>
            <div class="text" v-else><span>UID {{item.uid}}</span> 激励池奖励第{{index+1}}名，直推地址<font>{{item.child}}</font>个，获得奖金<font>{{item.num}} U</font></div>
            <div class="address">{{item.address}}</div>
          </div>
        </div>
      </div>
    </div>
    <app-bar></app-bar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      localeLanguage: localStorage.getItem('localeLanguage'),
      rewardList: [],
      rewardList2: [],
      stage: '',
    };
  },
  created() {
    this.getReward();
    this.getReward2();
  },
  methods: {
    // 中奖记录
    getReward() {
      this.$http("/jackpot/award").then(response => {
        const res = response.data.data;
        console.log(res.data);
        // this.rewardTitle = res.msg;
        if(res.data.length > 0) {
         this.rewardList = res.data;
        }
      })
    },
    // 奖池数据
    getReward2() {
      this.$http("/jackpot/inspire").then(response => {
        const res = response.data.data;
        console.log(res);
        if(res.data.length > 0) {
          this.stage = response.data.msg;
          this.rewardList2 = res.data;
        }
      })
    },
  }
};
</script>

