<template>
  <div class="news">
    <!-- <van-nav-bar title="市场奖" left-arrow @click-left="routerBack"/> -->
    <!-- <div class="top">
      <strong>{{id}}</strong><span>UID</span>
    </div> -->
    <van-list class="list" v-model="loading" :finished="finished" finished-text="" @load="onLoad">
      <div class="list-title">
        <div>{{$t('index.smartChips')}}</div>
        <div class="center" style="width: 30%!important;">{{$t('index.sourceID')}}</div>
        <div class="center" style="width: 30%!important;">{{$t('index.income')}} (USDT）</div>
        <div>{{$t('index.status')}}</div>
      </div>
      <div class="list-item" v-for="item in list" :key="item.id">
        <div>{{$t('index.the')}}{{item.stage_confirm}}{{$t('index.stage')}}</div>
        <div class="center" style="width: 30%!important;">{{item.invite_code}}</div>
        <div class="center color1" style="width: 30%!important;">{{item.num}}</div>
        <div class="color2" v-if="item.status == 0">{{$t('index.pendingSettlement')}}</div>
        <div v-if="item.status == 1">{{$t('index.settled')}}</div>
        <div class="color2" v-if="item.status == 2">{{$t('index.hasFailed')}}</div>
      </div>
    </van-list>
  </div>
</template>

<script>
export default {
  data () {
    return {
      id: this.$route.query.id,
      type: this.$route.query.type,
      list: [],
      loading: false,
      finished: false,
      page: 1,
    }
  },
  methods: {
    onLoad() {
      const params = {
        type: this.type,
        stage_confirm: this.id,
        page: this.page,
      }
      this.page++;
      this.$http('/wallet/profit', {params: params}).then(response => {
        let res = response.data.data;
        this.info = res;
        console.log(res);
        res.data.forEach(element => {
          this.list.push(element);
        });
        this.loading = false;
        if (res.data.length < res.per_page) {
          this.finished = true;
        }
      });
    },
  }
}
</script>

