<template>
  <div class="news">
    <van-tabs v-model="active" line-width="40">
      <van-tab :title="$t('index.introduction')">
        <van-list class="list" v-model="loading" :finished="finished" finished-text="" @load="onLoad">
          <router-link :to="{path: '/AboutUsDetails', query:{ item: JSON.stringify(item), type: 0}}" class="list-item" v-for="item in list" :key="item.id">
            <div class="item01">
              <span class="text">{{item.title}}</span>
            </div>
          </router-link>
        </van-list>
      </van-tab>
      <van-tab :title="$t('index.play')">
        <van-list class="list" v-model="loading2" :finished="finished2" finished-text="" @load="onLoad2">
          <router-link :to="{path: '/AboutUsDetails', query:{ item: JSON.stringify(item), type: 1}}" class="list-item" v-for="item in list2" :key="item.id">
            <div class="item01">
              <span class="text">{{item.title}}</span>
            </div>
          </router-link>
        </van-list>
      </van-tab>
      <van-tab :title="$t('index.data')">
        <van-list class="list" v-model="loading3" :finished="finished3" finished-text="" @load="onLoad3">
          <router-link :to="{path: '/AboutUsDetails', query:{ item: JSON.stringify(item), type: 2}}" class="list-item" v-for="item in list3" :key="item.id">
            <div class="item01">
              <span class="text">{{item.title}}</span>
            </div>
          </router-link>
        </van-list>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
export default {
  data () {
    return {
      active: 0,
      list: [],
      loading: false,
      finished: false,
      page: 1,
      list2: [],
      loading2: false,
      finished2: false,
      page2: 1,
      list3: [],
      loading3: false,
      finished3: false,
      page3: 1,
    }
  },
  created() {
    if(sessionStorage.getItem('aboutType')) {
      this.active = parseInt(sessionStorage.getItem('aboutType'));
    }
  },
  methods: {
    onLoad() {
      const params = {
        page: this.page,
        type: '1',
      }
      this.page++;
      this.$http('/index/about', {params: params}).then(response => {
        let res = response.data.data;
        console.log(res);
        res.data.forEach(element => {
          this.list.push(element);
        });
        this.loading = false;
        if (res.data.length < res.per_page) {
          this.finished = true;
        }
      });
    },
    onLoad2() {
      const params = {
        page: this.page2,
        type: '2',
      }
      this.page2++;
      this.$http('/index/about', {params: params}).then(response => {
        let res = response.data.data;
        console.log(res);
        res.data.forEach(element => {
          this.list2.push(element);
        });
        this.loading2 = false;
        if (res.data.length < res.per_page) {
          this.finished2 = true;
        }
      });
    },
    onLoad3() {
      const params = {
        page: this.page3,
        type: '3',
      }
      this.page2++;
      this.$http('/index/about', {params: params}).then(response => {
        let res = response.data.data;
        console.log(res);
        res.data.forEach(element => {
          this.list3.push(element);
        });
        this.loading3 = false;
        if (res.data.length < res.per_page) {
          this.finished3 = true;
        }
      });
    },
  }
}
</script>

