<template>
  <div class="address">
    <van-nav-bar :title="$t('index.languageSettings')"/>
    <ul class="list">
      <li class="list-item" @click="setLanguage('zh-cn')">
        <div class="left"><div>简体中文</div></div>
        <div class="right" v-if="localeLanguage == 'zh-cn'"><van-icon name="success"/></div>
      </li>
      <li class="list-item" @click="setLanguage('en-us')">
        <div class="left"><div>English</div></div>
        <div class="right" v-if="localeLanguage == 'en-us'"><van-icon name="success"/></div>
      </li>
    </ul>
    <van-button class="common-btn logout" @click="openLogin">{{$t('index.deleteAccount')}}</van-button>
    <!-- 退出登陆提示 -->
    <van-dialog v-model="showLogin" :showConfirmButton="false">
      <div class="out-title"><img src="../../assets/img/warning.png" alt=""></div>
      <div class="out-title">{{$t('index.deleteAccount')}}?</div>
      <div class="out-text">{{$t('index.logoutInfo')}}！</div>
      <div class="out-btn">
        <van-button class="common-btn common-btn2" @click="cancelOut">{{$t('index.cancel')}}</van-button>
        <van-button class="common-btn" @click="cancellation">{{$t('index.deleteAccount')}}</van-button>
      </div>
    </van-dialog>
  </div>
</template>

<script>
import { setup } from '@/locales' 
export default {
  data () {
    return {
      info: [],
      localeLanguage: localStorage.getItem('localeLanguage'),
      showLogin: false,
    }
  },
  methods: {
    setLanguage(lang) {
      setup(lang);
      this.$http.defaults.headers['Accept-Language'] = lang;
      this.localeLanguage = lang;
    },
    // 注销账号
    openLogin() {
      this.showLogin = true;
    },
    cancelOut() {
      this.showLogin = false;
    },
    // 注销账号
    cancellation() {
      this.$http.post("/user/logoff").then(response => {
        const res = response.data;
        console.log(res);
        this.$toast(res.msg);
        if(res.code === 0) {
          localStorage.clear();
          sessionStorage.clear();
          this.$http.defaults.headers.token = '';
          this.$http.defaults.headers.language = this.localeLanguage;
          this.$router.push('/Login');
        }
      });
    },
  }
}
</script>

