<template>
  <div class="login">
    <div class="group">
      <div class="imToken">
        <van-cell-group>
          <van-field v-if="show" :label="$t('index.inviteCode2')" label-width="64" v-model="invitecode" :placeholder="$t('index.inviteCode3')"/>
          <van-button class="common-btn" @click="submitImToen">{{$t('index.enter')}}</van-button>
        </van-cell-group>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      address: '',
      invitecode: '',
      sign: '',
      show: false,
      hex: '1', //0是字符串,1是0x字符串
    };
  },
  created() {
    // imToken方法
    if(!!window.imToken) {
      imToken.callAPI('user.showAccountSwitch', {chainType: 'ETHEREUM'}, (err, address) => {
        if(err) {
          this.$toast(err.message);
        } else {
          this.address = address;  //获得授权地址
          this.userSign();
          this.checkAddress();
        }
      })
    }else if(!!window.ethereum) {
      ethereum.enable()
      .catch((reason)=> {
        if (reason === "User rejected provider access") {
          // The user didn't want to sign in!
        } else {
          this.$toast("已拒绝连接");
        }
      })
      .then((accounts)=> {
        console.log(JSON.stringify(accounts));
        this.address = accounts[0];
        this.userSign();
        this.checkAddress();
      });
    }
  },
  mounted() {
    // let code = this.GetQueryString('invite_code');
    // if(!!code) {
    //   this.invitecode = code;
    // }
  },
  methods: {
    // 签名
    userSign() {
      let msg = '0x77656c636f6d6520746f20414d42'; //imtoken不转16进制 77656c636f6d6520746f20414d42
      if(!!window.imToken) {
        msg = 'welcome to AMB';  //welcome to AMB
        this.hex = '0';
      }
      let from = this.address;
      let params = [msg, from]
      let method = 'personal_sign'
      web3.currentProvider.sendAsync({method,params,from,}, (err, result)=> {
        if (err) return console.error(err)
        if (result.error) return console.error(result.error)
        console.log('PERSONAL SIGNED:' + JSON.stringify(result.result))
        this.sign = result.result;
      })
    },
    // 登录
    submitImToen() {
      const formData = {
        address: this.address,
        sign: this.sign,
        hex: this.hex,
      }
      if(this.invitecode) {
        formData.invite_code = this.invitecode;
      }
      this.$http.post('/login/imLogin', formData).then((response) => {
        const res = response.data;
        this.$toast(res.msg);
        if(res.code === 0) {
          this.$http.defaults.headers.token = res.data.token;
          localStorage.setItem('accessToken', res.data.token);
          this.$router.push('/Index');
        }
      })
    },
    // 正则获取地址栏参数
    GetQueryString(name) {
      let after = window.location.search.substr(1) || window.location.hash.split("?")[1];
      if(!after) {
        return null;
      }
      let reg = new RegExp("(^|&)"+ name +"=([^&]*)(&|$)");
      let r = after.match(reg);
      if(r!=null){
        return unescape(r[2]);
      }else{
        return null;
      } 
    },
    // 验证地址
    checkAddress() {
      const formData = {
        address: this.address,
      }
      this.$http.post('/login/chenkAddress', formData).then((response) => {
        const res = response.data;
        if(res.code !== 0) {
          this.show = true;
        }
      })
    },
    // 扫一扫
    scanQrCode() {
      imToken.callAPI('native.scanQRCode', (err, text)=> {
        if(err) {
          this.$toast(err.message);
        } else {
          // this.$toast(text);
          this.invitecode = text;
        }
      })
    },
  }
};
</script>

