    <!-- <div class="common-btn logout" style="color:#6d738d; margin-top:5px; background:none; font-size:13px; text-align:center;"><span @click="openLogin">{{$t('index.deleteAccount')}}</span></div> -->
    <!-- 退出登陆提示 -->
    <van-dialog v-model="showLogin" :showConfirmButton="false">
      <div class="out-title"><img src="../../assets/img/warning.png" alt=""></div>
      <div class="out-title">{{$t('index.deleteAccount')}}?</div>
      <div class="out-text">{{$t('index.logoutInfo')}}！</div>
      <div class="out-btn">
        <van-button class="common-btn common-btn2" @click="cancelOut">{{$t('index.cancel')}}</van-button>
        <van-button class="common-btn" @click="cancellation">{{$t('index.deleteAccount')}}</van-button>
      </div>
    </van-dialog>
    <app-bar></app-bar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')),
      config: JSON.parse(localStorage.getItem('config')),
      avatar: '',
      showLogin: false, //退出登陆提示
      localeLanguage: localStorage.getItem('localeLanguage'),
      showPwd: false, // 交易密码设置提示
      dealPassword: '',
      dealPassword2: '',
    };
  },
  created() {
    this.getUserInfo();
  },
  methods: {
    // 用户信息
    getUserInfo() {
      this.$http("/user/info").then(response => {
        const res = response.data.data;
        console.log(res);
        if(res.is_trading_password === 1) {
          this.showPwd = true;
        }
        localStorage.setItem("userInfo", JSON.stringify(res));
      });
    },
    // 注销账号
    openLogin() {
      this.showLogin = true;
    },
    cancelOut() {
      this.showLogin = false;
    },
    // 注销账号
    cancellation() {
      this.$http.post("/user/logoff").then(response => {
        const res = response.data;
        console.log(res);
        this.$toast(res.msg);
        if(res.code === 0) {
          localStorage.clear();
          sessionStorage.clear();
          this.$http.defaults.headers.token = '';
          this.$http.defaults.headers.language = this.localeLanguage;
          this.$router.push('/Login');
        }
      });
    },
    // 退出登陆
    Logout() {
      localStorage.clear();
      sessionStorage.clear();
      this.$http.defaults.headers.token = '';
      this.$http.defaults.headers.language = this.localeLanguage;
      localStorage.setItem("localeLanguage",this.localeLanguage)
      this.$router.push('/Login');
    },
    //复制地址
    onCopy: function (e) {
      this.$toast(this.$t('index.copyS'));
    },
    onError: function (e) {
      this.$toast(this.$t('index.copyF'));
    },
    // 暂未开放
    developing() {
      this.$toast('暂未开放');
    }
  }
};
</script>

