      <div class="title" style="margin-top:10px;" v-else>非常遗憾，<br>本期AMB智筹差一点就成功了，<br>感谢您的支持与参与！</div>
      <div class="text" v-if="localeLanguage == 'en-us'">At present, smart chips have activated the stop-loss settlement function. Players who meet the compensation rules and guaranteed fund pool rewards, please check their wallet balance in time. After the settlement, there will be a 24-hour rebirth preparation period, when we will be resurrected with full blood and start a new round of journey! We sincerely invite you to continue to support and participate in smart chips!</div> <div class="text" v-else>目前智筹已经启动止损结算功能，符合赔付规则与保障基金池奖励的玩家，请及时查看自己的钱包余额。结算之后，将有24小时的重生准备期，届时我们将满血复活，开启新一轮征程！诚意邀请您继续支持与参与智筹！</div>
      <div class="button" style="justify-content: center;" >
