      <div class="text" v-else>您所选择的第{{append_order}}期自动续投功能，因钱包余额不足而续费不成功。请及时进行充值，或使用手动续投功能。<br>续投不成功会导致利润降低，请及时调整。</div>
      <div class="button" style="margin-bottom:20px;">
