      <div class="text">您本期投资金额因低于标准投资范围的最低金额{{userStage.invest}} U，目前属于次级投资，利润将降低为12%。如果想要利润最大化，投资额须符合标准投资范围。</div>
      <div class="button" style="margin-bottom:20px;">
