        <van-button class="common-btn" @click="show8=false">{{$t('index.modify')}}</van-button>
      </div>
    </van-dialog>
    <!-- 利润降低提醒 -->
    <van-dialog v-model="show9" width="320" :showConfirmButton="false">
      <img class="warning" src="../assets/img/warning01.png" alt="">
      <div class="close" @click="show9=false"><van-icon name="cross" /></div>
      <div class="title" style="padding-top:35px;">{{$t('index.reminder2')}}</div>
      <div class="text" v-if="localeLanguage == 'en-us'">The {{userStage.stage}} smart chip you invested in will be renewed in the next {{userStage.stage+3}} stage. Due to the increase in the scope of the standard investment during this period, if your balance is insufficient, the renewal order may become a secondary investment, and the profit will be reduced to 12%. This reminds you to recharge in time to ensure maximum profit.</div>
      <div class="text" v-else>您所投资的第{{userStage.stage}}期智筹，将在下次第{{userStage.stage+3}}期续投。因该期标准投资范围增长，如果您的余额不足可能会导致续投订单变为次级投资，利润降低为12%。在此提醒您及时充值以确保利润最大化。</div>
      <div class="button" style="margin-bottom:20px;">
        <van-button class="common-btn cancel" to="/Recharge">{{$t('index.rechargeNow')}}</van-button>
        <van-button class="common-btn" to="/OrderLog">{{$t('index.renewNow')}}</van-button>
