    <!-- 所有期数众筹成功 -->
    <van-dialog v-model="show5" width="320" :showConfirmButton="false">
      <img src="../assets/img/success.png" alt="">
      <div class="title" style="margin-top:10px;">{{$t('index.congratulations1')}}<br>{{$t('index.congratulations2')}}</div>
      <div class="text">{{$t('index.congratulations3')}}</div>
      <div class="button" style="justify-content: center;" >
        <van-button class="common-btn cancel" style="margin-bottom:20px;" @click="show5=false">{{$t('index.iKnow')}}</van-button>
