</template>

<script>
export default {
  data() {
    return {
      localeLanguage: localStorage.getItem('localeLanguage'),
      status: 0,  //众筹状态
      currentRate: 0,
      rate: 0,
      gradientColor: {
        '0%': '#FEBA59',
        '100%': '#FF5A00',
      },
      times: 30*60*1000,
      show: false, // 预约弹出
      number: '10.00',
      typeId: 1,
      typeList: [
        {value:1, text: this.$t('index.goal1')},{value:2, text: this.$t('index.investmentMin')},{value:3, text: this.$t('index.sameInvestment')}
      ],
      autoDouble: true,
      show2: false, // 提醒续投弹出
      show3: false, // 回收弹出
      show4: false, // 众筹失败
      show5: false, // 所有众筹成功
      show6: false, // 续投余额不足
      show7: false, // 续投订单已延期
      show8: false, // 次级投资
      show9: false, // 利润降低提醒

      stageList: [{stage:'', status_text:''},{stage:'', status_text:''}], //期数
      stageCurrent: {num: 0, total: 0}, //当前期数
      userStage: {}, //用户选择预约期数
      config: {}, //配置
      timer: '',
      days: '00',
      hours: '00',
      minutes: '00',
      seconds: '00',
      userInfo: {},
      percent: 0,  //预约进度
      info: {1: 0,2:0,3:0,4:0},
      handle: false,
      is_append: false, //小红点
      appendInfo: {}, //续投信息
      isLoading: false,
      last_invest: '', //上次投资
      append_order: 1, //续投期数
      is_delay: 0, //延期
      stageTips: [8,9,10,18,19,20,28,29,30,33,34,35,38,39,40],
      showNotice:false,
      handleList: [],
      submitDisabled: false, //提交按钮
      submitDisabled2: false, //提交按钮
    }
  },
  created() {
    this.getUserInfo();
    this.getStage();
    this.getConfig();
    this.getInfo();
    setTimeout(() => {
      this.getNotice();
    }, 500);
  },
  computed: {
    stageListComputed() {
      let arr = this.stageList.slice(1);
      arr.forEach(element => {
        element.isDisabled = true;
      });
      for (let index = 0; index < arr.length; index++) {
        if((!!arr[index].opid && !arr[index].oid && (!!this.stageCurrent.oid || (parseFloat(this.stageCurrent.last) <= 0))) || (!arr[index].opid && !arr[index].oid) && parseFloat(arr[index].last) > 0 ) {
          arr[index].isDisabled = false;
          break;
        }
      }
      return arr;
    },
    profit() {
      if(parseFloat(this.number)>=parseFloat(this.userStage.invest)) {
        return (this.number*0.15).toFixed(2);
      }
      if(parseFloat(this.number)<parseFloat(this.userStage.invest)) {
        return (this.number*0.12).toFixed(2);
      }
    }
  },
  methods: {
    // 奖池数据
    getInfo() {
      this.$http("/jackpot/pool").then(response => {
        const res = response.data;
        // console.log(res);
        this.info = res.data;
      })
    },
    // 用户信息
    getUserInfo() {
      this.$http("/user/info").then(response => {
        const res = response.data.data;
        // console.log(res);
        this.userInfo = res;
        this.money = res.usdt;
        localStorage.setItem("userInfo", JSON.stringify(res));
      });
    },
    // 倒计时结束触发
    finished() {
      setTimeout(() => {
        this.getStage();
      }, 2000);
    },
    // 期数
    getStage() {
      this.$http("/stage/stage").then(response => {
        const res = response.data.data;
        console.log(res);
        if(res.status == 2) {
          this.status = res.status;
          this.show4 = true;   //失败
          return;
        }
        if(res.status == 1) {
          this.status = res.status;
          this.show5 = true;  //所有众筹成功
          return;
        }
        if(parseFloat(res.last_invest) <= 0) {
          this.typeList = [{value:1, text: this.$t('index.goal1')},{value:2, text: this.$t('index.investmentMin')}] 
        }else{
          this.last_invest = res.last_invest;
        }
        if(res.first.length>0) {
          this.handleList = res.first;
          // let date = new Date().getTime();
          this.handleList.forEach(element => {
            let timestamp = (element.end_stage-element.start_stage);
            // console.log(timestamp)
            element.time = parseInt(timestamp/86400) + this.$t('index.day') + parseInt(timestamp%86400/3600) + this.$t('index.hour') + parseInt(timestamp%3600/60) + this.$t('index.min');
          });
        }
        if(res.list.length>0) {
          this.stageList = [];
          res.list.forEach((element,index) => {
            // element.time = element.create_time - element.create_time;
            this.stageList.push(element);
          });
          this.stageCurrent = res.list[0];
          if(res.is_append.count !== 0) {
            this.appendInfo = res.is_append;
            this.is_append = true; //小红点
            if(!sessionStorage.getItem('show2')){
              this.show2 = true;  
              sessionStorage.setItem('show2','1');
            }
          }
          if(res.is_delay !== 0) {
            this.is_delay = res.is_delay;
            if(!sessionStorage.getItem('show7')){
              this.show7 = true;  
              sessionStorage.setItem('show7','1');
            }
          }
          if(res.append_order !==0) {
            this.append_order = res.append_order;
            if(!sessionStorage.getItem('show6')){
              this.show6 = true;  
              sessionStorage.setItem('show6','1');
            }
          }
          this.rate = this.stageCurrent.num/this.stageCurrent.total*100;  //百分比
          if(parseFloat(this.stageCurrent.last) > 0) {
            let now = new Date().getTime();
            let end = this.stageCurrent.end_time*1000;
            this.times = parseInt(end-now)/1000;
            this.timer = setInterval(()=> {
              if(this.times > 0){
                this.days = Math.floor(this.times/86400);
                this.hours = Math.floor((this.times%86400)/3600);
                this.minutes = Math.floor(((this.times%86400)%3600)/60);  
                this.seconds = Math.floor(((this.times%86400)%3600)%60);
                if (this.days <= 9) this.days = '0' + this.days;
                if (this.hours <= 9) this.hours = '0' + this.hours;
                if (this.minutes <= 9) this.minutes = '0' + this.minutes;
                if (this.seconds <= 9) this.seconds = '0' + this.seconds;
              }else{
                this.second = "00";
                clearInterval(this.timer);
                // this.finished();
              }
              this.times--;
            },1000);
          }else{
            // 筹满
            this.times = parseInt((this.stageCurrent.end_time-this.stageCurrent.start_time)-(this.stageCurrent.end_stage-this.stageCurrent.start_stage));
            // console.log(this.times)
            this.days = Math.floor(this.times/86400);
            this.hours = Math.floor((this.times%86400)/3600);
            this.minutes = Math.floor(((this.times%86400)%3600)/60);  
            this.seconds = Math.floor(((this.times%86400)%3600)%60);
            if (this.days <= 9) this.days = '0' + this.days;
            if (this.hours <= 9) this.hours = '0' + this.hours;
            if (this.minutes <= 9) this.minutes = '0' + this.minutes;
            if (this.seconds <= 9) this.seconds = '0' + this.seconds;
          }
          localStorage.setItem("stageList", JSON.stringify(res));
        }
      })
    },
    // 配置
    getConfig() {
      this.$http("/index/config").then(response => {
        const res = response.data.data;
        // // console.log(res);
        this.config = res;
        localStorage.setItem('config', JSON.stringify(res));
      });
    },
    // 预约弹出
    openShow(stage) {
      this.show = true;
      this.userStage = stage;
      if(this.typeId == 1) {
        this.number = (this.userStage.total/100).toFixed(2);
      }else if(this.typeId == 2) {
        this.number = parseFloat(this.userStage.invest).toFixed(2);
      }else if(this.typeId == 3) {
        this.number = parseFloat(this.last_invest).toFixed(2);
      }
    },
    changeAppend() {
      if(this.typeId == 1) {
        this.number = (this.userStage.total/100).toFixed(2);
      }else if(this.typeId == 2) {
        this.number = parseFloat(this.userStage.invest).toFixed(2);
      }else if(this.typeId == 3) {
        this.number = parseFloat(this.last_invest).toFixed(2);
      }
    },
    // 选择类型
    selectType(value) {
      this.append = value;
    },
    // 次级投资判断
    checkSubmit() {
      this.submitDisabled = true;
      setTimeout(() => {
        this.submitDisabled = false;
      }, 3000);
      if(parseFloat(this.number) > parseFloat(this.userInfo.usdt)) {
        this.$toast('您的钱包余额不足');
        return;
      }
      if(parseFloat(this.number) < parseFloat(this.userStage.invest) && parseFloat(this.number)>=10) {
        this.show8 = true;
      }else{
        this.submit();
      }
    },
    // 预约提交
    submit() {
      this.submitDisabled2 = true;
      setTimeout(() => {
        this.submitDisabled2 = false;
      }, 3000);
      this.show8 = false;
      let append = 1;  //自动
      if(!this.autoDouble) {
        append = 0;
      }
      const formData = {
        num: this.number,
        append: append,
      }
      this.$http.post('/stage/invest', formData).then((response) => {
        const res = response.data;
        this.$toast(res.msg);
        if(res.code === 0) {
          this.show = false;
          this.getStage();
          this.getUserInfo();
          if(this.stageTips.indexOf(this.userStage.stage) != -1) {
            setTimeout(() => {
              this.show9 = true;
            }, 200);
          }
        }
      })
    },
    // 展开收缩
    handleChange() {
      this.handle = !this.handle;
    },
    // 下拉刷新
    onRefresh() {
      this.getStage();
      setTimeout(() => {
        this.isLoading = false;
      }, 1000);
    },
    // notice
    getNotice() {
      this.$http('/index/notice').then(response => {
        let res = response.data.data;
        // console.log(res);
        let len = parseInt(localStorage.getItem('noticeLen'));
        if(!localStorage.getItem('noticeLen')) {
          len = 0;
        }
        if(res.data.length>len) {
          this.showNotice = true;
        }
      });
    },
  },
  destroyed() {
    clearInterval(this.timer);
  },
};
</script>

