<template>
  <div class="team">
    <!-- <van-nav-bar style="background-color:#121A33;" @click-left="routerBack">
      <router-link slot="right" style="padding-top: 32px;" to="/TeamCount"><van-icon size="22" name="friends-o"/></router-link>
    </van-nav-bar> -->
    <div class="all">{{$t('index.totalBonus')}}：<strong>{{profit.toFixed(2)}}</strong></div>
    <router-link class="friends" to="/TeamCount"><van-icon size="22" name="friends-o"/></router-link>
    <van-tabs v-model="active" type="card" line-width="40">
      <van-tab :title="$t('index.promotionAward')">
        <div class="top">
          <div class="text"><strong>{{(info['0']).toFixed(2)}}</strong><span>{{$t('index.totalreward')}} (USDT) </span></div>
          <div class="text"><strong>{{info['1']}}</strong><span>{{$t('index.shareAddress')}}</span></div>
          <div class="text"><strong>{{info['2']}}</strong><span>{{$t('index.shareAddress2')}}</span></div>
        </div>
        <van-list class="list" v-model="loading" :finished="finished" finished-text="" @load="onLoad">
          <div class="list-title">
            <div class="">{{$t('index.smartChips')}}</div>
            <div class="width33">{{$t('index.dueIncome')}} (USDT)</div>
            <div class="">{{$t('index.remainingOpen')}} (USDT）</div>
          </div>
          <router-link :to="{path: '/TeamDetails', query: {id: item.stage_confirm, type: '1'}}" class="list-item" v-for="item in list" :key="item.id">
            <div class="">{{$t('index.the')}}{{item.stage_confirm}}{{$t('index.stage')}}</div>
            <div class="width33">{{item.total}}</div>
            <div class="color1 play"><span>{{item.last}}</span><van-icon name="play" /></div>
          </router-link>
        </van-list>
      </van-tab>
      <van-tab :title="$t('index.marketAward')">
        <div class="top">
          <div class="text"><strong>{{parseFloat(info2['0']).toFixed(2)}}</strong><span>{{$t('index.totalreward')}} (USDT) </span></div>
          <div class="text"><strong>{{parseFloat(info2['1']).toFixed(2)}}</strong><span>{{$t('index.flowStatistics')}} (USDT)</span></div>
          <div class="text"><strong>VIP{{userInfo.level}}</strong><span>{{$t('index.myLevel')}}</span></div>
        </div>
        <van-list class="list" v-model="loading2" :finished="finished2" finished-text="" @load="onLoad2">
          <div class="list-title">
            <div class="">{{$t('index.smartChips')}}</div>
            <div class="width33">{{$t('index.dueIncome')}} (USDT)</div>
            <div class="">{{$t('index.remainingOpen')}} (USDT）</div>
          </div>
          <router-link :to="{path: '/TeamDetails', query: {id: item.stage_confirm, type: '2'}}" class="list-item" v-for="item in list2" :key="item.id">
            <div class="">{{$t('index.the')}}{{item.stage_confirm}}{{$t('index.stage')}}</div>
            <div class="width33">{{item.total}}</div>
            <div class="color1">{{item.last}}<van-icon name="play" /></div>
          </router-link>
        </van-list>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
export default {
  data () {
    return {
      profit: 0,
      userInfo: JSON.parse(localStorage.getItem('userInfo')),
      info: {'0': 0, '1':0, '2':0},
      info2: {'0': 0, '1':0, '2':0},
      active: 0,
      list: [],
      loading: false,
      finished: false,
      page: 1,
      list2: [],
      loading2: false,
      finished2: false,
      page2: 1,
    }
  },
  methods: {
    onLoad() {
      const params = {
        page: this.page,
        type: '1'
      }
      this.page++;
      this.$http('/wallet/profitStage', {params: params}).then(response => {
        let res = response.data.data;
        this.info = res.row;
        this.profit = res.profit;
        console.log(res);
        res.data.forEach(element => {
          this.list.push(element);
        });
        this.loading = false;
        if (res.data.length < res.per_page) {
          this.finished = true;
        }
      });
    },
    // 分享
    onLoad2() {
      const params = {
        page: this.page2,
        type: '2',
      }
      this.page2++;
      this.$http('/wallet/profitStage', {params: params}).then(response => {
        let res = response.data.data;
        console.log(res);
        this.info2 = res.row;
        res.data.forEach(element => {
          this.list2.push(element);
        });
        this.loading2 = false;
        if (res.data.length < res.per_page) {
          this.finished2 = true;
        }
      });
    },
  }
}
</script>

