<template>
  <div class="wallet-log">
    <!-- <van-nav-bar title="余额与记录" left-arrow @click-left="routerBack"/> -->
    <div class="top">
      <div class="eye">{{$t('index.available')}}（USDT）<van-icon name="eye-o" @click="showMoney"/></div>
      <div class="num" v-if="show">{{userInfo.usdt}}
        <!-- <van-icon name="replay" @click="refresh"/> -->
      </div>
      <div class="num" v-else>****</div>
      <div v-if="show">≈{{(userInfo.usdt*7).toFixed(2)}} CNY</div>
      <div v-else>****</div>
    </div>
    <div class="log">{{$t('index.fundRecords')}}</div>
    <!-- USDT -->
    <van-list class="list" v-model="loading" :finished="finished" :finished-text="$t('index.noMore')" @load="onLoad">
      <div class="list-item" v-for="item in list" :key="item.id">
        <div class="item01">
          <span class="text">{{item.type_text}}</span>
          <span class="num" v-if="item.act==1">+{{item.num}}</span>
          <span class="num color1" v-else>-{{item.num}}</span>
        </div>
        <div class="item02">{{item.create_time}}<br><span>{{$t('index.number')}}</span></div>
      </div>
    </van-list>
    <div class="nav">
      <router-link to="/Recharge"><img src="../../assets/img/coin01.png" alt=""><div>{{$t('index.recharge')}}</div></router-link>
      <router-link to="/Withdraw"><img src="../../assets/img/coin02.png" alt=""><div>{{$t('index.withdrawal')}}</div></router-link>
      <router-link to="/Transfer"><img src="../../assets/img/coin03.png" alt=""><div>{{$t('index.transfer')}}</div></router-link>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')),
      show: true,
      list: [],
      loading: false,
      finished: false,
      page: 1,
    }
  },
  methods: {
    // usdt充币记录
    onLoad() {
      const params = {
        page: this.page,
        type: ''
      }
      this.page++;
      this.$http('/wallet/usdtLog', {params: params}).then(response => {
        let res = response.data.data;
        this.info = res;
        console.log(res);
        res.data.forEach(element => {
          this.list.push(element);
        });
        this.loading = false;
        if (res.data.length < res.per_page) {
          this.finished = true;
        }
      });
    },
    // 显示money
    showMoney() {
      this.show = !this.show;
    },
    // 刷新
    refresh() {

    }
  }
}
</script>

