<template>
  <div class="notices">
    <!-- <van-nav-bar title="系统消息"  left-arrow @click-left="routerBack"/> -->
    <van-list class="list" v-model="loading" :finished="finished" finished-text="" @load="onLoad">
      <router-link class="list-item" v-for="item in list" :key="item.id" :to="{path:'/NoticesDetails',query:{ item: JSON.stringify(item)}}">
        <div class="left">
          <div><span>{{item.title}}</span></div>
        </div>
        <div class="right">
          <!-- <div>&nbsp;</div> -->
          <div class="time">{{item.create_time}}</div>
        </div>
      </router-link>
    </van-list>
  </div>
</template>

<script>
export default {
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      page: 1,
    }
  },
  methods: {
    onLoad() {
      const params = {
        page: this.page,
      }
      this.page++;
      this.$http('/index/notice', {params: params}).then(response => {
        let res = response.data.data;
        console.log(res);
        res.data.forEach(element => {
          this.list.push(element);
        });
        this.loading = false;
        localStorage.setItem('noticeLen', res.data.length)
        if (res.data.length < res.per_page) {
          this.finished = true;
        }
      });
    },
  }
}
</script>

