<template>
  <div class="point-trade">
    <!-- <van-nav-bar title="互转" left-arrow @click-left="routerBack"/> -->
    <div class="top">
			<div class="left">USDT</div>
			<div>ERC20</div>
		</div>
    <van-cell-group>
			<div class="lable">{{$t('index.transferAddress')}}</div>
      <van-field style="padding-bottom:6px;" type="textarea" rows="2" v-model="validateForm.address" :placeholder="$t('index.inputAddress')" right-icon="scan" @click-right-icon="scanQrCode" clearable>
        <template #button>
          <van-icon name="friends-o" @click="toAddress()" class="address"/>
        </template>
      </van-field>
			<div class="lable">{{$t('index.transferNumber')}}</div>
      <van-field v-model="validateForm.number" type="number" :placeholder="$t('index.least10')">
        <template #button style="line-height: 12px;">
          <font>USDT</font>
          <span class="all" @click="all">{{$t('index.all')}}</span>
        </template>
      </van-field>
      <div class="money">{{$t('index.available')}} <span>{{userInfo.usdt}}</span> USDT</div>
      <div class="tips">{{$t('index.transferInfo')}}</div>
      <van-button class="common-btn" @click.prevent="userSign()">{{$t('index.confirm')}}</van-button>
    </van-cell-group>
  </div>   
</template>

<script>
export default {
  data() {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')),
      validateForm: {
        address: '',
        number: '',
      },
      sign: '',
      hex: '1', //0是字符串,1是0x字符串
    };
  },
  created() {
    if(localStorage.getItem('address')) {
      this.validateForm.address = localStorage.getItem('address');
    };
  },
  methods: {
    // 签名
    userSign() {
      if(!this.validateForm.address) {
        this.$toast(this.$t('index.inputAddress'));
        return;
      }
      if(parseFloat(this.validateForm.number) < 10 || !this.validateForm.number) {
        this.$toast(this.$t('index.least10'));
        return;
      }
      let msg = '0x7472616e736665722055534454'; //imtoken不转16进制 7472616e736665722055534454
      if(!!window.imToken) {
        msg = 'transfer USDT';  //welcome to AMB
        this.hex = '0';
      }
      let from = this.userInfo.username;
      let params = [msg, from]
      let method = 'personal_sign'
      web3.currentProvider.sendAsync({method,params,from,}, (err, result)=> {
        if (err) return console.error(err)
        if (result.error) return console.error(result.error)
        this.sign = result.result;
        this.submit();
      })
    },
    // 提交
    submit() {
      const formData = {
        address: this.validateForm.address,
        num: this.validateForm.number,
        sign: this.sign,
        hex: this.hex,
      }
      this.$http.post('/wallet/transfer', formData).then((response) => {
        const res = response.data;
        this.$toast(res.msg);
        if(res.code === 0) {
          this.$router.back();
        }
      })
    },
    // 全部
    all() {
      this.validateForm.number = parseFloat(this.userInfo.usdt);
    },
    // 扫一扫
    scanQrCode() {
      let that = this;
      imToken.callAPI('native.scanQRCode', function (err, text) {
        if(err) {
          that.$toast(err.message);
        } else {
          // that.$toast(text);
          that.validateForm.address = text;
        }
      })
    },
    // 去地址薄
    toAddress() {
      this.$router.push({path: '/AddressList', query: {type: this.type}})
    },
  }
};
</script>
<style scoped>
.top{ width: 92%; margin: 10px 4%; padding: 12px 4%; display: flex; justify-content: space-between; background: #121A33; font-size: 15px; border-radius:5px;}
.top .left{ color: #B9BFD9;}
.common-btn{ margin-top: 30px; }
.money { padding: 10px 0; }
.money span{ color: #B9BFD9;}
.van-cell .address{ font-size:22px; color: #6D738D; margin-top: 6px; margin-left: 10px;}
.van-cell font{ color: #6D738D;}
.all{ color: #1AC3E9; font-size: 14px; margin-left: 15px;}
.rate{ padding: 12px 0; display: flex; justify-content: space-between; align-items: center; font-size: 14px; border-bottom: 1px solid #19223E}
.tips{ padding: 10px 0; color: #B9BFD9;}
</style> 
